// Job single offer styles

.job-single-offer-page {
    @include clearfix;
    height: 100vh;
    overflow-y: auto;

    .main-container {
        display: block !important;

        .container-navigation {
            padding: 27px 20px 20px;
            border-bottom: 1px solid $border-color;

            .page-title {
                .sidebar-page-nav-container {
                    display: -webkit-flex;
                    display: -moz-flex;
                    display: -ms-flex;
                    display: -o-flex;
                    display: flex;
                    margin-bottom: 5px;

                    .page-navigation {
                        display: -moz-inline-flex;
                        display: -ms-inline-flex;
                        display: -o-inline-flex;
                        display: inline-flex;
                        align-items: center;
                        padding: 0px 15px 0 0;
                        align-self: flex-start;
                        color: #9A9FA9;
                    }

                    .sidebar-title {
                        margin: 0;
                        font-size: 18px;
                        color: #353F53;
                        font-weight: 600;
                        line-height: normal;
                    }
                }
            }
        }

        .container-content {
            @include clearfix;

            width: 93.699%;
            padding: 0;
            margin: 25px auto;

            [class*='-content-main'] {
                .offer-banner {
                    width: 100%;
                    height: 240px;
                    padding: 0;
                    border-radius: 4px 4px 0 0;
                    overflow: hidden;

                    .offer-banner-overlay {
                        width: 100%;
                        height: 100%;
                        position: absolute;

                        /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/$dark-gray+0,ffffff+100&1+0,0+100 */
                        background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.5) 100%);
                        /* FF3.6-15 */
                        background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.5) 100%);
                        /* Chrome10-25,Safari5.1-6 */
                        background: linear-gradient(to bottom, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.5) 100%);
                        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#24251f', endColorstr='#141411', GradientType=0);
                        /* IE6-9 */
                    }

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }

                    input[type='file'] {
                        display: none;
                    }

                    .custom-file-upload {
                        display: -webkit-inline-flex;
                        display: -moz-inline-flex;
                        display: -ms-inline-flex;
                        display: -o-inline-flex;
                        display: inline-flex;
                        align-items: center;
                        justify-content: center;
                        padding: 6px 10px;
                        margin: 0;
                        cursor: pointer;
                        position: absolute;
                        right: 15px;
                        top: 15px;
                        color: #fff;
                        font-size: 14px;
                        font-weight: normal;
                        text-shadow: 2px 2px 4px #353F53;

                        span {
                            text-decoration: underline;
                        }

                        i {
                            font-size: 16px;
                            margin-left: 5px;
                        }

                        &:hover {
                            span {
                                text-decoration: none;
                            }
                        }
                    }

                    a {
                        color: #fff;

                        &.change-banner {
                            input[type='file'] {
                                position: absolute;
                                height: 100%;
                                width: 100%;
                                top: 0;
                                left: 0;
                                opacity: 0;

                                &:hover {
                                    cursor: pointer;
                                }
                            }

                            display: -webkit-inline-flex;
                            display: -moz-inline-flex;
                            display: -ms-inline-flex;
                            display: -o-inline-flex;
                            display: inline-flex;
                            align-items: center;
                            justify-content: center;
                            font-size: 14px;
                            position: absolute;
                            right: 25px;
                            top: 15px;

                            span {
                                text-decoration: underline;
                            }

                            i {
                                margin-left: 10px;
                                font-size: 14px;
                            }

                            &:hover {
                                cursor: pointer;

                                span {
                                    text-decoration: none;
                                }

                                input {
                                    cursor: pointer;

                                    &:hover {
                                        cursor: pointer;
                                    }
                                }
                            }
                        }
                    }

                    h3 {
                        margin: 0;
                        position: absolute;
                        bottom: 15px;
                        left: 15px;
                        color: #fff;
                        transition: all .3s ease;
                        width: calc(100% - 40px);

                        &.offer-title {
                            text-shadow: 2px 2px 4px #353F53;

                            .btn-yes {
                                border-radius: 0 4px 4px 0;
                            }
                        }
                    }
                }

                .offer-content {
                    padding: 40px 20px 20px 20px;
                    position: relative;
                    color: $main;

                    span.edit {
                        position: absolute;
                        right: 20px;
                        top: 20px;

                        &:hover {
                            cursor: pointer;
                        }
                    }

                    [class$='-edit'] {
                        display: none;
                    }

                    h4,
                    p {
                        color: $main;
                    }

                    h4 {
                        font-size: 16px;
                        font-weight: 600;
                        margin: 0;
                    }

                    p {
                        font-size: 14px;
                    }

                    a {
                        color: $selected;
                        text-decoration: underline;

                        &:hover {
                            text-decoration: none;
                        }
                    }

                    .no-data-placeholder {

                        h4,
                        p {
                            color: $helper;
                        }
                    }
                }
            }

            [class*='-content-aside'] {
                .section-title {
                    color: $main;
                    font-weight: 600;
                    padding: 0;
                    margin: 0;
                }

                label {
                    font-weight: 600;
                }

                .card {
                    margin-bottom: 10px;

                    &:last-child {
                        margin: 0;
                    }
                }

                [class*='-info'] {
                    .language-to {
                        @extend %language-breadcrum;

                        li {
                            a {
                                &:after {
                                    border-left-color: #fff !important;
                                }
                            }
                        }
                    }

                    .offer-bidding {
                        padding: 10px 20px 0px;

                        .bidding-price {
                            font-size: 24px;
                            font-weight: 700;
                            color: $main;
                            text-align: center;
                            padding: 10px;

                            .fa-database {
                                display: -webkit-inline-flex;
                                display: -moz-inline-flex;
                                display: -ms-inline-flex;
                                display: -o-inline-flex;
                                display: inline-flex;
                                margin-right: 10px;
                                position: relative;

                                .fa-database {
                                    position: absolute;
                                    left: 0.5em;
                                    width: 0.8em;

                                    /* height: 0.8em; */
                                    top: 0.4em;
                                    font-size: 0.8em;
                                    background: white;
                                }
                            }

                            span {
                                font-weight: normal;
                            }
                        }

                        .bidding-contributors {
                            margin: 20px 0;

                            .applied-contributors {
                                display: -webkit-flex;
                                display: -moz-flex;
                                display: -ms-flex;
                                display: -o-flex;
                                display: flex;
                                justify-content: center;
                                flex-flow: row wrap;

                                .user-image {
                                    margin: 0 10px 15px 0px;

                                    &:last-child {
                                        margin-right: 0;
                                    }
                                }
                            }
                        }
                    }

                    [class$='-actions'] {
                        margin-bottom: 25px;
                    }

                    [class*='-actions-more'] {
                        .section-title {
                            color: $main;
                            font-weight: 600;
                            padding: 0;
                            margin: 0 0 10px 0;
                        }

                        [class*='-actions-list'] {
                            padding: 0;
                            margin: 0;
                            list-style-type: none;

                            li {
                                @include clearfix;

                                .card {
                                    padding: 7px 15px;
                                    margin-bottom: 10px;
                                    color: $main;

                                    .edit {
                                        position: absolute;
                                        right: 15px;
                                    }

                                    span.help {
                                        position: absolute;
                                        right: 15px;
                                    }
                                }

                                &:last-child {
                                    .card {
                                        margin: 0;
                                    }
                                }
                            }
                        }
                    }

                    .eligibility-filters {
                        @include clearfix;

                        padding: 10px;
                        border-radius: 4px;
                        background: $highlight-01;

                        .select-with-label {
                            @extend %select-with-label;

                            .select-with-label-disable {
                                padding: 10px 15px;
                                cursor: not-allowed !important;
                                opacity: 1 !important;
                                background-color: #d7d9dc !important;
                                border-color: #d7d9dc !important;
                                color: #fff !important;
                                border-radius: 6px !important;
                            }
                        }

                        .more-filters {
                            display: none;
                        }

                        .toggle-more-filters {
                            transition: all .3s ease;

                            &.open {
                                i {
                                    transform: rotateX(0);
                                }
                            }

                            i {
                                transform: rotateX(-180deg);
                                margin-right: 5px;
                            }

                            span {
                                text-decoration: underline;
                            }

                            &:hover {
                                cursor: pointer;

                                span {
                                    text-decoration: none;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

/*Sourcing navigation left Styles Starts*/
.nav {
    &.container-nav {
        padding: 0;
        border: 1px solid $border-color;
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        border-radius: 4px;

        li {
            display: -webkit-inline-flex;
            display: -moz-inline-flex;
            display: -ms-inline-flex;
            display: -o-inline-flex;
            display: inline-flex;
            flex: 1;
            transition: all .3s ease;
            border-right: 1px solid $border-color;

            a {
                display: -webkit-flex;
                display: -moz-flex;
                display: -ms-flex;
                display: -o-flex;
                display: flex;
                flex: 1;
                padding: 8px;
                align-items: center;
                justify-content: center;
                font-size: 14px;
                font-weight: 600;
            }

            &.active,
            &:hover {
                background: $selected;

                a {
                    color: #fff;
                }
            }

            &:first-child {
                border-radius: 4px 0 0 4px;
            }

            &:last-child {
                border-radius: 0px 4px 4px 0;
                border-right: none;
            }
        }
    }
}

/*Sourcing navigation left Styles Ends*/

/* Modal styles */
.edit-description-modal {
    @extend %modal;
}

.sourcing-manage-filters {
    @extend %modal;

    label {
        font-weight: 600;
        text-transform: uppercase;
        font-size: 12px;

        >span {
            &:last-child {
                color: $main;
                font-weight: 600;
            }
        }
    }

    .sec-title {
        color: $main;
        font-weight: 600;
        margin-bottom: 5px;
        display: block;
    }

    .filters-container {
        border-bottom: 2px solid $divider-color;
        margin: 0 0 10px;
    }

    .filters-subsec {
        margin: 15px 0 15px 15px;
        padding: 0;
        border-left: 2px solid $divider-color;
    }

    [class*='add-filter-'] {
        color: $selected;
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    }

    .exclusion-filters-list {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        padding: 0;
        margin: 0;
        flex-flow: row wrap;
        padding-top: 10px;

        li {
            display: -webkit-inline-flex;
            display: -moz-inline-flex;
            display: -ms-inline-flex;
            display: -o-inline-flex;
            display: inline-flex;
            width: calc(100% / 3);
            margin: 0 0px 5px 0;

            label {
                margin: 0;
            }
        }
    }
}

/* Modal styles ends*/
