/* Clients page styles */
.layout-3.wp-app {
    .page-wrap {
        .main-panel {
            .main-panel-page {
                .main-container.clients-list-page {
                    /* tab panel container -->*/
                    .tab-panel-container {
                        .status-list-container {
                            margin: 0 0 20px 0;

                            .status-list {
                                ul {
                                    display: -webkit-box;
                                    display: -ms-flexbox;
                                    display: flex;
                                    -ms-flex-wrap: wrap;
                                    flex-wrap: wrap;
                                    margin: 0;

                                    li,
                                    li:nth-child(3) {
                                        -webkit-box-flex: 1;
                                        -ms-flex-positive: 1;
                                        flex-grow: 1;
                                        margin: 0 10px 10px 0;
                                    }
                                }
                            }
                        }

                        .filter-list-container {
                            margin-bottom: 30px;

                            .filter-list {
                                .owners {
                                    #filterPeriod {
                                        .form-inline {
                                            display: -webkit-box;
                                            display: -ms-flexbox;
                                            display: flex;
                                            -webkit-box-pack: justify;
                                            -ms-flex-pack: justify;
                                            justify-content: space-between;
                                        }
                                    }
                                }
                            }
                        }
                    }

                    /* tab panel container <--*/
                    /* main panel container -->*/
                    .main-panel-container {
                        .content {
                            .tab-content {
                                .tab-content-container {
                                    .client-content-container {
                                        .client-content-container {
                                            [class*='-list-table'] {
                                                .client-list-item {
                                                    box-shadow: rgba(
                                                            50,
                                                            50,
                                                            50,
                                                            0.1
                                                        )
                                                        0px 0px 5px 0px;

                                                    &:hover {
                                                        box-shadow: rgba(
                                                                68,
                                                                138,
                                                                255,
                                                                0.1
                                                            )
                                                            0px 0px 5px 0px;
                                                    }
                                                }
                                            }
                                        }

                                        &:nth-of-type(1) {
                                            margin-top: 10px;
                                        }

                                        [class*='-list-table'] {
                                            @extend %bootstraptable;
                                        }
                                    }
                                }
                            }
                        }
                    }

                    /* main panel container <--   */
                }
            }
        }
    }
}

/* modal window styles*/
.modal.create-new-client-modal,
.modal.create-new-contact-modal {
    *.d-block {
        display: block;
    }

    @extend %modal;

    .modal-content {
        /* padding: 15px; */
        font-size: 12px;
        color: #9ca1ab;

        /*     span.close {
      position: absolute;
      top: -4px;
      right: -4px;
    } */

        label,
        a {
            color: #9ca1ab;
        }

        .modal-header {
        }

        .modal-body {
            .modal-title {
                font-size: 20px;
                color: #353f53;
                font-weight: 600;
                margin: 5px 0 20px 0;
            }

            .form {
                label {
                    /* color: #9ca1ab;*/
                    /* font-size: 12px;*/
                    font-weight: $font-weight-normal;

                    &:hover {
                        cursor: pointer;
                    }
                }

                .form-group {
                    /*                     .row {
                                            [class*='col-'] {
                                                padding: 0 10px 0 0;

                                                &:first-child {
                                                    padding-left: 15px;
                                                }

                                                &:last-child {
                                                    padding-right: 15px;
                                                }
                                            }

                                            [class*='-12'] {
                                                padding: 0 15px;
                                            }
                                        } */

                    input[type='file'] {
                        display: none;
                    }

                    a[class*='add-'] {
                        @extend %add-button;

                        height: 120px;
                        border: 2px dashed $border-color;
                        font-size: 14px;
                        font-weight: 600;
                        box-shadow: none;

                        span {
                            font-weight: $font-weight-normal;
                        }

                        &:hover,
                        &:focus {
                            border: 2px solid $selected;
                        }
                    }

                    a[class*='import-from'] {
                        text-decoration: underline;
                        margin: 10px 0;
                    }

                    .btn {
                        width: 100% !important;
                    }
                }
            }
        }

        .modal-footer {
        }
    }

    &.create-new-client-modal {
        .client-logo {
            &.full {
                display: flex;
                align-items: center;

                .image-container {
                    .image-loaded {
                        max-height: 90px;
                    }
                }

                .client-desc-container {
                    a {
                        text-decoration: underline;

                        &:hover {
                            text-decoration: none;
                        }
                    }

                    .description {
                        a {
                            &.client-name {
                                color: #448aff;
                                font-weight: 600;
                            }
                        }
                    }

                    .image-action {
                        a {
                            display: flex;
                            align-items: center;
                            margin-right: 20px;
                            position: relative;

                            &.remove {
                                color: #ee0a5f;
                                padding-left: 15px;

                                &:before {
                                    content: '\E5CD';
                                    font-family: 'Material Icons';
                                    position: absolute;
                                    top: 0px;
                                    left: 0;
                                }
                            }
                        }
                    }
                }
            }
        }

        .form-group {
            &.divider {
                padding-top: 20px;
                border-top: 2px solid #f5f5f6;
            }
        }

        /* Chosen select styles starts */
        .chosen-container {
            &.chosen-container-single {
                color: #353f53;

                // font-weight: 600;
            }
        }

        .chose-image-list,
        .chose-image-small {
            // border-radius: 3px;
            margin: 0px 13px 0 0;
        }

        /* Chosen select styles ends */
    }

    &.create-new-contact-modal {
        @extend %modal;

        .form {
            label {
                &.main-contact {
                    text-transform: none;
                }
            }

            .contact-pic {
                @extend %contact-pic;
            }

            .form-group {
                &.divider {
                    padding-top: 20px;
                    border-top: 2px solid #f5f5f6;
                }
            }
        }
    }
}
