// Sourcing list styles

.sourcing-list-page {
    .main-container {
        .main-panel-container {
            .content {
                .tab-content {
                    .main-panel-header {
                        .page-status {
                            text-align: left !important;
                        }

                        .sort-offers {
                            margin-top: 22px;
                        }
                    }

                    .card-section-list {
                        ul {
                            li {
                                .card-mission {
                                    &.quote-mission-creation {
                                        height: auto !important;
                                    }

                                    .list-head-container {
                                        width: 47%;
                                        padding-right: 0;
                                    }

                                    @-moz-document url-prefix() {
                                        .list-head-container {
                                            width: 46%;
                                        }
                                    }
                                }

                                .card {
                                    .task-price {
                                        width: 234px;
                                        display: -webkit-flex;
                                        display: -moz-flex;
                                        display: -ms-flex;
                                        display: -o-flex;
                                        display: flex;
                                        justify-content: flex-end;

                                        .turnover {
                                            &.draft {
                                                margin-top: 14px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .no-data-container {
                .create {
                    margin: 0 auto;
                    display: block;
                }
            }
        }
    }
}

/* Offers Job Create Starts */

.create-offer-job-modal,
.quizz-surveys-creation-modal {
    .modal-title {
        font-size: 20px;
        color: #353f53;
        font-weight: 600;
        margin-bottom: 10px;
        padding: 0;
    }

    .modal-title-edit {
        /* display: none; */
    }

    .create-offers-job-settings {
        .chosen-container-single .chosen-single {
            height: 38px;
        }

        .chosen-container-single .chosen-single div b {
            background-position: -2px 10px;
        }

        .general-settings-container {
            .general-settings-module {
                margin-bottom: 10px;

                .include-check {
                    font-weight: $font-weight-normal;
                }

                #upload-general-settings-file {
                    display: none;
                }

                .add-general-settings-file {
                    display: -webkit-box;
                    display: -moz-box;
                    display: -ms-flexbox;
                    display: -webkit-flex;
                    display: flex;
                    -webkit-flex-flow: row wrap;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 65px;
                    color: #D7D9DC;
                    text-decoration: underline;
                    text-align: center;
                    border: 2px dashed #D7D9DC;
                    border-radius: 4px;
                    transition: all .3s ease;

                    &:hover,
                    &:focus {
                        border: 2px dashed #448AFF;
                        color: #448AFF;
                    }
                }

                .added-general-settings-file-container {
                    display: -webkit-box;
                    display: -moz-box;
                    display: -ms-flexbox;
                    display: -webkit-flex;
                    display: flex;
                    -webkit-flex-flow: row wrap;
                    align-items: center;
                    justify-content: center;
                    height: 65px;
                    border: 2px solid #D7D9DC;
                    border-radius: 4px;

                    .added-general-settings-file {
                        width: 100%;

                        i {
                            line-height: 1.6;
                        }

                        .file-name {
                            display: -webkit-box;
                            display: -moz-box;
                            display: -ms-flexbox;
                            display: -webkit-flex;
                            display: flex;
                            -webkit-flex-flow: row wrap;
                            align-items: center;
                            height: 38px;
                        }
                    }
                }

                .search-user {
                    input[type='text'] {
                        background: url('../img/search.svg') no-repeat;
                        background-size: 25px;
                        background-position: 10px 5px;
                        padding-left: 50px;
                    }
                }

                .stakeholder {
                    .update-contract-owner {
                        span.edit {
                            i {
                                font-size: 16px;
                                top: 4px;
                            }
                        }
                    }
                }
            }
        }

        .availabilities-container {
            .available-requirements {
                p {
                    font-size: 14px;
                }
            }

            .availabilities-module {
                margin-bottom: 10px;
            }
        }

        .quizz-survey-container {
            .quizz-survey-module {
                margin-bottom: 10px;

                .new-template-selection {
                    padding: 15px 50px;
                    background: #f7f9fa;
                    border-radius: 4px;
                }

                .create-template {
                    color: #353f53;
                    text-decoration: underline;

                    &:hover,
                    &:focus {
                        text-decoration: none;
                    }
                }
            }
        }

        .job-tests-container {
            .job-tests-module {
                margin-bottom: 10px;

                &.test-job-title {
                    margin-top: 12px;
                }

                #upload-tests-file {
                    display: none;
                }

                .add-tests-file {
                    display: -webkit-box;
                    display: -moz-box;
                    display: -ms-flexbox;
                    display: -webkit-flex;
                    display: flex;
                    -webkit-flex-flow: row wrap;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 65px;
                    color: #D7D9DC;
                    text-decoration: underline;
                    text-align: center;
                    border: 2px dashed #D7D9DC;
                    border-radius: 4px;
                    transition: all .3s ease;

                    &:hover,
                    &:focus {
                        border: 2px dashed #448AFF;
                        color: #448AFF;
                    }
                }

                .added-tests-file-container {
                    display: -webkit-box;
                    display: -moz-box;
                    display: -ms-flexbox;
                    display: -webkit-flex;
                    display: flex;
                    -webkit-flex-flow: row wrap;
                    align-items: center;
                    justify-content: center;
                    height: 65px;
                    border: 2px solid #D7D9DC;
                    border-radius: 4px;

                    .added-tests-file {
                        width: 100%;

                        i {
                            line-height: 1.6;
                        }

                        .file-name {
                            display: -webkit-box;
                            display: -moz-box;
                            display: -ms-flexbox;
                            display: -webkit-flex;
                            display: flex;
                            -webkit-flex-flow: row wrap;
                            align-items: center;
                            height: 38px;
                        }
                    }
                }
            }
        }
    }
}

/* Sourcing list Ends */

/* Sourcing Create modal Starts */

.create-sourcing-modal {
    .modal-title {
        font-size: 20px;
        color: #353f53;
        font-weight: 600;
        margin-bottom: 10px;
        padding: 0;
    }

    .modal-title-edit {
        display: none;

        button {
            width: 140px !important;
        }
    }

    .create-sourcing-container {
        h4.title {
            font-size: 16px;
        }

        .chosen-container {
            margin-bottom: 10px;
        }

        .chosen-container-single .chosen-single {
            height: 38px;
        }

        .chosen-container-single .chosen-single div b {
            background-position: -2px 10px;
        }

        .create-client-link {
            a {
                text-decoration: underline;
                color: #353f53;

                &:hover,
                &:focus {
                    text-decoration: none;
                }
            }
        }
    }

    .tab-navigation {
        margin-top: 0;
    }
}

/* Sourcing Create modal Ends */
