form{
        
    label{
        &.radio,
        &.checkbox{
            font-size: 14px;
            text-transform: none;
            cursor: pointer;
        }
    }
}

.form-horizontal{
    .checkbox,  
    .radio{
        padding-top: 0;
        
        &:first-child,
        &.checkbox-inline,
        &.radio-inline{
            margin-top: 10px;    
        }
    }
}

star{
    color: $danger-color;
    padding-left: 3px;
}

@media (min-width: $screen-md-min){
    .form-horizontal{
        .control-label{
            padding-top: 12px !important;
        }
        code{
            margin-top: 8px;
            display: inline-block;
        }
    }
}