/*!
 * FullCalendar v2.4.0 Stylesheet
 * Docs & License: http://fullcalendar.io/
 * (c) 2015 Adam Shaw
 */

.fc {
    direction: ltr;
    text-align: left;
}

.fc-rtl {
    text-align: right;
}

body .fc {
    /* extra precedence to overcome jqui */
    font-size: 1em;
}

/* Colors
  --------------------------------------------------------------------------------------------------*/

.fc-unthemed {

    th,
    td,
    thead,
    tbody,
    .fc-divider,
    .fc-row {
        border-color: $medium-gray;
    }

    .fc-divider {
        background: $divider-color;
    }

    .fc-popover {
        border-color: $medium-gray;
        background-color: $white-color;
        border-width: 1px;
        border-style: solid;
        .fc-header {
            background: $divider-color;

            .fc-close {
                color: $link-disabled-color;
                font-size: .9em;
                margin-top: 2px;
            }
        }
    }

    .fc-today {
        background: $smoke-bg;
    }
}

.fc-highlight {
    /* when user is selecting cells */
    background: #bce8f1;
    opacity: .3;
    filter: alpha(opacity=30);

    /* for IE */
}

.fc-bgevent {
    /* default look for background events */
    background: #8fdf82;
    opacity: .3;
    filter: alpha(opacity=30);

    /* for IE */
}

.fc-nonbusiness {
    /* default look for non-business-hours areas */
    /* will inherit .fc-bgevent's styles */
    background: $default-color-top;
}

/* Icons (inline elements with styled text that mock arrow icons)
  --------------------------------------------------------------------------------------------------*/

.fc-icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    line-height: 1em;
    font-size: 1em;
    text-align: center;
    overflow: hidden;
    font-family: 'Courier New', Courier, monospace;

    /* don't allow browser text-selection */
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &:after {
        position: relative;
        margin: 0 -1em;

        /* ensures character will be centered, regardless of width */
    }
}

/*
  Acceptable font-family overrides for individual icons:
    'Arial', sans-serif
    'Times New Roman', serif

  NOTE: use percentage font sizes or else old IE chokes
  */

.fc-icon-left-single-arrow:after {
    content: '\02039';
    font-weight: bold;
    font-size: 200%;
    top: -7%;
    left: 3%;
}

.fc-icon-right-single-arrow:after {
    content: '\0203A';
    font-weight: bold;
    font-size: 200%;
    top: -7%;
    left: -3%;
}

.fc-icon-left-double-arrow:after {
    content: '\000AB';
    font-size: 160%;
    top: -7%;
}

.fc-icon-right-double-arrow:after {
    content: '\000BB';
    font-size: 160%;
    top: -7%;
}

.fc-icon-left-triangle:after {
    content: '\25C4';
    font-size: 125%;
    top: 3%;
    left: -2%;
}

.fc-icon-right-triangle:after {
    content: '\25BA';
    font-size: 125%;
    top: 3%;
    left: 2%;
}

.fc-icon-down-triangle:after {
    content: '\25BC';
    font-size: 125%;
    top: 2%;
}

.fc-icon-x:after {
    content: '\000D7';
    font-size: 200%;
    top: 6%;
}

/* Buttons (styled <button> tags, normalized to work cross-browser)
  --------------------------------------------------------------------------------------------------*/

.fc button {
    border-width: 1px;
    background-color: transparent;
    font-weight: $font-weight-normal;
    opacity: 0.8;
    filter: alpha(opacity=80);
    padding: 8px 16px;
    border-color: $default-color;
    color: $default-color;

    &:hover,
    &:focus {
        opacity: 1;
        filter: alpha(opacity=100);
        outline: 0 !important;
    }

    &:active,
    &.active {
        -webkit-box-shadow: none;
        box-shadow: none;
        outline: 0 !important;
    }
}

.open>.fc button.dropdown-toggle {
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: 0 !important;
}

/* Firefox has an annoying inner border */

.fc button::-moz-focus-inner {
    margin: 0;
    padding: 0;
}

.fc-state-default {
    /* non-theme */
    border: 1px solid;
    /* Added from custom_styles */
    background-color: #fff !important;
    background-image: none !important;
    background-repeat: none !important;
    border-color: #448AFF !important;
    color: #448AFF !important;
    text-shadow: 0 1px 1px rgba(255, 255, 255, 0.75);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
  

    &.fc-corner-left {
        /* non-theme */
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
    }

    &.fc-corner-right {
        /* non-theme */
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
    }
}

/* icons in buttons */

.fc button .fc-icon {
    /* non-theme */
    position: relative;
    top: -0.05em;

    /* seems to be a good adjustment across browsers */
    margin: 0 .2em;
    vertical-align: middle;
}

/*
    button states
    borrowed from twitter bootstrap (http://twitter.github.com/bootstrap/)
  */

.fc-state-active,
.fc-state-disabled {
    color: $black-color;
    background-color: $light-gray;
}

.fc button[disabled] {
    background-color: $light-gray;
    border-color: $light-gray;
    cursor: default;
    opacity: 0.65;
    filter: alpha(opacity=65);

    &:focus,
    &:hover {
        background-color: $light-gray;
        border-color: $light-gray;
        cursor: default;
        opacity: 0.65;
        filter: alpha(opacity=65);
    }
}

.fc .fc-state-active {
    background-color: $default-color;
    color: $white-color;

    &:focus,
    &:hover {
        background-color: $default-color;
        color: $white-color;
    }

    &:active {

        &:focus,
        &:hover {
            background-color: $default-color;
            color: $white-color;
        }

        background-color: $default-color;
        color: $white-color;
    }
}

/* Buttons Groups
  --------------------------------------------------------------------------------------------------*/

.fc-button-group {
    display: inline-block;
}

/*
  every button that is not first in a button group should scootch over one pixel and cover the
  previous button's border...
  */

.fc .fc-button-group> {
    * {
        /* extra precedence b/c buttons have margin set to zero */
        float: left;
        margin: 0 0 0 -1px;
    }

    :first-child {
        /* same */
        margin-left: 0;
    }
}

/* Popover
  --------------------------------------------------------------------------------------------------*/

.fc-popover {
    position: absolute;
    -webkit-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.125);
    box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.125);

    .fc-header {
        /* TODO: be more consistent with fc-head/fc-body */
        padding: 2px 4px;

        .fc-title {
            margin: 0 2px;
        }

        .fc-close {
            cursor: pointer;
        }
    }
}

.fc-ltr .fc-popover .fc-header .fc-title {
    float: left;
}

.fc-rtl .fc-popover .fc-header {
    .fc-close {
        float: left;
    }

    .fc-title {
        float: right;
    }
}

.fc-ltr .fc-popover .fc-header .fc-close {
    float: right;
}

/* unthemed */



/* jqui themed */

.fc-popover>.ui-widget-header+.ui-widget-content {
    border-top: 0;

    /* where they meet, let the header have the border */
}

/* Misc Reusable Components
  --------------------------------------------------------------------------------------------------*/

.fc-divider {
    border-style: solid;
    border-width: 1px;
}

hr.fc-divider {
    height: 0;
    margin: 0;
    padding: 0 0 2px;

    /* height is unreliable across browsers, so use padding */
    border-width: 1px 0;
}

.fc-clear {
    clear: both;
}

.fc-bg,
.fc-bgevent-skeleton,
.fc-highlight-skeleton,
.fc-helper-skeleton {
    /* these element should always cling to top-left/right corners */
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.fc-bg {
    bottom: 0;

    /* strech bg to bottom edge */
    table {
        height: 100%;

        /* strech bg to bottom edge */
    }
}

/* Tables
  --------------------------------------------------------------------------------------------------*/

.fc {
    table {
        width: 100%;
        table-layout: fixed;
        border-collapse: collapse;
        border-spacing: 0;
        font-size: 1em;

        /* normalize cross-browser */
    }

    th {
        text-align: right;
        border-bottom-width: 1px;
        font-size: 14px;
        text-transform: uppercase;
        color: $dark-gray;
        font-weight: $font-weight-normal;
        padding-bottom: 3px;
    }

    td {
        border-style: solid;
        border-width: 1px;
        padding: 0;
        vertical-align: top;

        &.fc-today {
            border-style: double;

            /* overcome neighboring borders */
        }
    }

    .fc-widget-header {
        border: 0;
    }

    .fc-head .fc-widget-header {
        margin-top: 15px;
    }

    .fc-row {
        /* extra precedence to overcome themes w/ .ui-widget-content forcing a 1px border */
        /* no visible border by default. but make available if need be (scrollbar width compensation) */
        border-style: solid;
        border-width: 0;
    }
}

/* Fake Table Rows
  --------------------------------------------------------------------------------------------------*/

.fc-row {
    table {
        /* don't put left/right border on anything within a fake row.
         the outer tbody will worry about this */
        border-left: 0 hidden transparent;
        border-right: 0 hidden transparent;

        /* no bottom borders on rows */
        border-bottom: 0 hidden transparent;
    }

    &:first-child table {
        border-top: 0 hidden transparent;

        /* no top border on first row */
    }

    position: relative;

    .fc-bg {
        z-index: 1;
    }

    .fc-bgevent-skeleton,
    .fc-highlight-skeleton {
        bottom: 0;

        /* stretch skeleton to bottom of row */
    }

    .fc-bgevent-skeleton table {
        height: 100%;

        /* stretch skeleton to bottom of row */
    }

    .fc-highlight-skeleton {
        table {
            height: 100%;

            /* stretch skeleton to bottom of row */
        }

        td {
            border-color: transparent;
        }
    }

    .fc-bgevent-skeleton {
        td {
            border-color: transparent;
        }

        z-index: 2;
    }

    .fc-highlight-skeleton {
        z-index: 3;
    }

    .fc-content-skeleton {
        position: relative;
        z-index: 4;
        padding-bottom: 2px;

        /* matches the space above the events */
    }

    .fc-helper-skeleton {
        z-index: 5;
    }

    .fc-content-skeleton td,
    .fc-helper-skeleton td {
        /* see-through to the background below */
        background: none;

        /* in case <td>s are globally styled */
        border-color: transparent;

        /* don't put a border between events and/or the day number */
        border-bottom: 0;
    }

    .fc-content-skeleton tbody td,
    .fc-helper-skeleton tbody td {
        /* don't put a border between event cells */
        border-top: 0;
    }
}

/* Day Row (used within the header and the DayGrid)
  --------------------------------------------------------------------------------------------------*/

/* highlighting cells & background event skeleton */

/*
  row content (which contains day/week numbers and events) as well as 'helper' (which contains
  temporary rendered events).
  */

/* Scrolling Container
  --------------------------------------------------------------------------------------------------*/

.fc-scroller {
    /* this class goes on elements for guaranteed vertical scrollbars */
    overflow-y: scroll;
    overflow-x: hidden;

    >* {
        /* we expect an immediate inner element */
        position: relative;

        /* re-scope all positions */
        width: 100%;

        /* hack to force re-sizing this inner element when scrollbars appear/disappear */
        overflow: hidden;

        /* don't let negative margins or absolute positioning create further scroll */
    }
}

/* Global Event Styles
  --------------------------------------------------------------------------------------------------*/

.fc-event {
    position: relative;

    /* for resize handle and other inner positioning */
    display: block;

    /* make the <a> tag block */
    font-size: .85em;
    line-height: 1.3;
    border-radius: 2px;
    background-color: rgba(68, 125, 247, 0.2);

    /* default BACKGROUND color */
    font-weight: normal;

    /* undo jqui's ui-widget-header bold */
    &.event-azure {
        background-color: rgba(35, 204, 239, 0.2);
    }

    &.event-green {
        background-color: rgba(135, 203, 22, 0.2);
    }

    &.event-orange {
        background-color: rgba(255, 165, 52, 0.2);
    }

    &.event-red {
        background-color: rgba(251, 64, 75, 0.2);
    }

    color: $black-color;

    /* default TEXT color */
    text-decoration: none;

    /* if <a> has an href */
    &:hover {
        color: $black-color;

        /* default TEXT color */
        text-decoration: none;

        /* if <a> has an href */
    }
}

/* overpower some of bootstrap's and jqui's styles on <a> tags */

.ui-widget .fc-event {
    color: $black-color;

    /* default TEXT color */
    text-decoration: none;

    /* if <a> has an href */
}

.fc-event {

    &[href],
    &.fc-draggable {
        cursor: pointer;

        /* give events with links and draggable events a hand mouse pointer */
    }
}

.fc-not-allowed {
    /* to override an event's custom cursor */
    cursor: not-allowed;

    .fc-event {
        /* to override an event's custom cursor */
        cursor: not-allowed;
    }
}

.fc-event {
    .fc-bg {
        /* the generic .fc-bg already does position */
        z-index: 1;
        background: $white-color;
        opacity: .25;
        filter: alpha(opacity=25);

        /* for IE */
    }

    .fc-content {
        position: relative;
        z-index: 2;
    }

    .fc-resizer {
        position: absolute;
        z-index: 3;
    }
}

/* Horizontal Events
  --------------------------------------------------------------------------------------------------*/
/* events that are continuing to/from another week. kill rounded corners and butt up against edge */

.fc-ltr .fc-h-event.fc-not-start,
.fc-rtl .fc-h-event.fc-not-end {
    margin-left: 0;
    border-left-width: 0;
    padding-left: 1px;

    /* replace the border with padding */
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.fc-ltr .fc-h-event.fc-not-end,
.fc-rtl .fc-h-event.fc-not-start {
    margin-right: 0;
    border-right-width: 0;
    padding-right: 1px;

    /* replace the border with padding */
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

/* resizer */

.fc-h-event .fc-resizer {
    /* positioned it to overcome the event's borders */
    top: -1px;
    bottom: -1px;
    left: -1px;
    right: -1px;
    width: 5px;
}

/* left resizer  */

.fc-ltr .fc-h-event .fc-start-resizer {
    right: auto;

    /* ignore the right and only use the left */
    cursor: w-resize;

    &:before,
    &:after {
        right: auto;

        /* ignore the right and only use the left */
        cursor: w-resize;
    }
}

.fc-rtl .fc-h-event .fc-end-resizer {
    right: auto;

    /* ignore the right and only use the left */
    cursor: w-resize;

    &:before,
    &:after {
        right: auto;

        /* ignore the right and only use the left */
        cursor: w-resize;
    }
}

/* right resizer */

.fc-ltr .fc-h-event .fc-end-resizer {
    left: auto;

    /* ignore the left and only use the right */
    cursor: e-resize;

    &:before,
    &:after {
        left: auto;

        /* ignore the left and only use the right */
        cursor: e-resize;
    }
}

.fc-rtl .fc-h-event .fc-start-resizer {
    left: auto;

    /* ignore the left and only use the right */
    cursor: e-resize;

    &:before,
    &:after {
        left: auto;

        /* ignore the left and only use the right */
        cursor: e-resize;
    }
}

/* DayGrid events
  ----------------------------------------------------------------------------------------------------
  We use the full 'fc-day-grid-event' class instead of using descendants because the event won't
  be a descendant of the grid when it is being dragged.
  */

.fc-day-grid-event {
    margin: 2px 5px 0;

    /* spacing between events and edges */
    padding: 1px 2px;

    .fc-content {
        /* force events to be one-line tall */
        white-space: nowrap;
        overflow: hidden;
    }

    .fc-time {
        font-weight: bold;
    }

    .fc-resizer {
        /* enlarge the default hit area */
        left: -3px;
        right: -3px;
        width: 7px;
    }
}

/* Event Limiting
  --------------------------------------------------------------------------------------------------*/
/* 'more' link that represents hidden events */

a.fc-more {
    margin: 1px 3px;
    font-size: .85em;
    cursor: pointer;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

.fc-limited {
    /* rows and cells that are hidden because of a 'more' link */
    display: none;
}

/* popover that appears when 'more' link is clicked */

.fc-day-grid .fc-row {
    z-index: 1;

    /* make the 'more' popover one higher than this */
}

.fc-more-popover {
    z-index: 2;
    width: 220px;

    .fc-event-container {
        padding: 10px;
    }
}

/* Toolbar
  --------------------------------------------------------------------------------------------------*/

.fc-toolbar {
    text-align: center;
    margin-bottom: 1em;

    .fc-left {
        float: left;
        //   min-width: 260px;
    }

    .fc-right {
        float: right;
    }

    .fc-center {
        display: inline-block;
    }
}

/* the things within each left/right/center section */

.fc .fc-toolbar>*> {
    * {
        /* extra precedence to override button border margins */
        float: left;
        margin-left: .75em;
    }

    :first-child {
        /* extra precedence to override button border margins */
        margin-left: 0;
    }
}

/* the first thing within each left/center/right section */

/* title text */

.fc-toolbar {
    h2 {
        margin: 0;
    }

    button {
        position: relative;
    }

    .fc-state-hover,
    .ui-state-hover {
        z-index: 2;
    }

    .fc-state-down {
        z-index: 3;
    }

    .fc-state-active,
    .ui-state-active {
        z-index: 4;
    }

    button:focus {
        z-index: 5;
    }
}

/* button layering (for border precedence) */

/* View Structure
  --------------------------------------------------------------------------------------------------*/
/* undo twitter bootstrap's box-sizing rules. normalizes positioning techniques */
/* don't do this for the toolbar because we'll want bootstrap to style those buttons as some pt */

.fc-view-container * {
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;

    &:before,
    &:after {
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
    }
}

.fc-view {
    /* so dragged elements can be above the view's main element */
    position: relative;
    z-index: 1;

    >table {
        /* so dragged elements can be above the view's main element */
        position: relative;
        z-index: 1;
    }
}

/* BasicView
  --------------------------------------------------------------------------------------------------*/
/* day row structure */

.fc-basicWeek-view .fc-content-skeleton,
.fc-basicDay-view .fc-content-skeleton {
    /* we are sure there are no day numbers in these views, so... */
    padding-top: 1px;

    /* add a pixel to make sure there are 2px padding above events */
    padding-bottom: 1em;

    /* ensure a space at bottom of cell for user selecting/clicking */
}

.fc-basic-view .fc-body .fc-row {
    min-height: 4em;

    /* ensure that all rows are at least this tall */
}

/* a 'rigid' row will take up a constant amount of height because content-skeleton is absolute */

.fc-row.fc-rigid {
    overflow: hidden;

    .fc-content-skeleton {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }
}

/* week and day number styling */

.fc-basic-view {

    .fc-week-number,
    .fc-day-number {
        padding: 0 2px;
    }

    td {

        &.fc-week-number span,
        &.fc-day-number {
            padding: 8px;
        }
    }

    .fc-week-number {
        text-align: center;

        span {
            /* work around the way we do column resizing and ensure a minimum width */
            display: inline-block;
            min-width: 1.25em;
        }
    }
}

.fc-ltr .fc-basic-view .fc-day-number {
    text-align: right;
}

.fc-rtl .fc-basic-view .fc-day-number {
    text-align: left;
}

.fc-day-number.fc-other-month {
    opacity: 0.3;
    filter: alpha(opacity=30);

    /* for IE */
    /* opacity with small font can sometimes look too faded
       might want to set the 'color' property instead
       making day-numbers bold also fixes the problem */
}

/* AgendaView all-day area
  --------------------------------------------------------------------------------------------------*/

.fc-agenda-view .fc-day-grid {
    position: relative;
    z-index: 2;

    /* so the 'more..' popover will be over the time grid */
    .fc-row {
        min-height: 3em;

        /* all-day section will never get shorter than this */
        .fc-content-skeleton {
            padding-top: 1px;

            /* add a pixel to make sure there are 2px padding above events */
            padding-bottom: 1em;

            /* give space underneath events for clicking/selecting days */
        }
    }
}

/* TimeGrid axis running down the side (for both the all-day area and the slot area)
  --------------------------------------------------------------------------------------------------*/

.fc .fc-axis {
    /* .fc to overcome default cell styles */
    vertical-align: middle;
    padding: 0 4px;
    white-space: nowrap;
}

.fc-ltr .fc-axis {
    text-align: right;
}

.fc-rtl .fc-axis {
    text-align: left;
}

.ui-widget td.fc-axis {
    font-weight: normal;

    /* overcome jqui theme making it bold */
}

/* TimeGrid Structure
  --------------------------------------------------------------------------------------------------*/

.fc-time-grid-container {
    /* so slats/bg/content/etc positions get scoped within here */
    position: relative;
    z-index: 1;
}

.fc-time-grid {
    /* so slats/bg/content/etc positions get scoped within here */
    position: relative;
    z-index: 1;
    min-height: 100%;

    /* so if height setting is 'auto', .fc-bg stretches to fill height */
    table {
        /* don't put outer borders on slats/bg/content/etc */
        border: 0 hidden transparent;
    }

    >.fc-bg {
        z-index: 1;
    }

    .fc-slats,
    >hr {
        /* the <hr> AgendaView injects when grid is shorter than scroller */
        position: relative;
        z-index: 2;
    }

    .fc-bgevent-skeleton,
    .fc-content-skeleton {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }

    .fc-bgevent-skeleton {
        z-index: 3;
    }

    .fc-highlight-skeleton {
        z-index: 4;
    }

    .fc-content-skeleton {
        z-index: 5;
    }

    .fc-helper-skeleton {
        z-index: 6;
    }

    .fc-slats {
        td {
            height: 1.5em;
            border-bottom: 0;

            /* each cell is responsible for its top border */
        }

        .fc-minor td {
            border-top-style: dotted;
        }

        .ui-widget-content {
            /* for jqui theme */
            background: none;

            /* see through to fc-bg */
        }
    }

    .fc-highlight-container {
        /* a div within a cell within the fc-highlight-skeleton */
        position: relative;

        /* scopes the left/right of the fc-highlight to be in the column */
    }

    .fc-highlight {
        position: absolute;
        left: 0;
        right: 0;

        /* top and bottom will be in by JS */
    }

    .fc-event-container,
    .fc-bgevent-container {
        /* a div within a cell within the fc-bgevent-skeleton */
        position: relative;
    }
}

/* TimeGrid Slats (lines that run horizontally)
  --------------------------------------------------------------------------------------------------*/

/* TimeGrid Highlighting Slots
  --------------------------------------------------------------------------------------------------*/

/* TimeGrid Event Containment
  --------------------------------------------------------------------------------------------------*/

.fc-ltr .fc-time-grid .fc-event-container {
    /* space on the sides of events for LTR (default) */
    margin: 0 2.5% 0 2px;
}

.fc-rtl .fc-time-grid .fc-event-container {
    /* space on the sides of events for RTL */
    margin: 0 2px 0 2.5%;
}

.fc-time-grid {
    .fc-event {
        position: absolute;
        z-index: 1;

        /* scope inner z-index's */
    }

    .fc-bgevent {
        position: absolute;
        z-index: 1;

        /* scope inner z-index's */
        /* background events always span full width */
        left: 0;
        right: 0;
    }
}

/* Generic Vertical Event
  --------------------------------------------------------------------------------------------------*/

.fc-v-event {
    &.fc-not-start {
        /* events that are continuing from another day */
        /* replace space made by the top border with padding */
        border-top-width: 0;
        padding-top: 1px;

        /* remove top rounded corners */
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }

    &.fc-not-end {
        /* replace space made by the top border with padding */
        border-bottom-width: 0;
        padding-bottom: 1px;

        /* remove bottom rounded corners */
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }
}

/* TimeGrid Event Styling
  ----------------------------------------------------------------------------------------------------
  We use the full 'fc-time-grid-event' class instead of using descendants because the event won't
  be a descendant of the grid when it is being dragged.
  */

.fc-time-grid-event {
    overflow: hidden;

    /* don't let the bg flow over rounded corners */
    .fc-time,
    .fc-title {
        padding: 0 1px;
    }

    .fc-time {
        font-size: .85em;
        white-space: nowrap;
    }

    &.fc-short {
        .fc-content {
            /* don't wrap to second line (now that contents will be inline) */
            white-space: nowrap;
        }

        .fc-time,
        .fc-title {
            /* put the time and title on the same line */
            display: inline-block;
            vertical-align: top;
        }

        .fc-time {
            span {
                display: none;

                /* don't display the full time text... */
            }

            &:before {
                content: attr(data-start);

                /* ...instead, display only the start time */
            }

            &:after {
                content: '\000A0-\000A0';

                /* seperate with a dash, wrapped in nbsp's */
            }
        }

        .fc-title {
            font-size: .85em;

            /* make the title text the same size as the time */
            padding: 0;

            /* undo padding from above */
        }
    }

    .fc-resizer {
        left: 0;
        right: 0;
        bottom: 0;
        height: 8px;
        overflow: hidden;
        line-height: 8px;
        font-size: 11px;
        font-family: monospace;
        text-align: center;
        cursor: s-resize;

        &:after {
            content: '=';
        }
    }
}

/* short mode, where time and title are on the same line */

/* resizer */

.card-calendar {
    .content {
        padding: 0 !important;
    }

    .fc-toolbar {
        padding-top: 15px;
        padding-left: 15px;
        padding-right: 15px;
    }

    .fc td {
        &:first-child {
            border-left: 0;
        }

        &:last-child {
            border-right: 0;
        }
    }

    .fc-basic-view td:last-child {

        &.fc-week-number span,
        &.fc-day-number {
            padding-right: 15px;
        }
    }

    .fc {
        .fc-day-header:last-child {
            padding-right: 15px;
        }

        .fc-row:last-child td,
        .fc-body .fc-widget-content {
            border-bottom: 0;
        }
    }
}
