/*           Font Smoothing      */
body,
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6,
p,
.navbar,
.brand,
.btn-simple,
.alert,
a,
.td-name,
td,
button.close {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-weight: $font-weight-normal;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4 {
    // font-weight: $font-weight-light;
    font-weight: $font-weight-normal; // Added from wp-old-styles
    color: #383d49; // Added from wp-old-styles
    margin: $margin-large-vertical 0 $margin-base-vertical;
}

h1, .h1 {
    // font-size: $font-size-h1;
    font-size: 32px; // Added from wp-old-styles
}

h2, .h2 {
    // font-size: $font-size-h2;
    font-size: 28px; // Added from wp-old-styles
}

h3, .h3 {
    // font-size: $font-size-h3;
    font-size: 22px; // Added from wp-old-styles
    margin: 20px 0 10px;
}

h4, .h4 {
    // font-size: $font-size-h4;
    font-size: 18px; // Added from wp-old-styles
    line-height: 30px;
}

h5, .h5 {
    // font-size: $font-size-h5;
    font-size: 14px; // Added from wp-old-styles
    margin-bottom: 15px;
}

h6, .h6 {
    // font-size: $font-size-h6;
    font-size: 11px; // Added from wp-old-styles
    font-weight: $font-weight-bold;
    text-transform: uppercase;
}

p {
    font-size: $font-paragraph;
    line-height: $line-height-general;
}

h1 small, h2 small, h3 small, h4 small, h5 small, h6 small, .h1 small, .h2 small, .h3 small, .h4 small, .h5 small, .h6 small, h1 .small, h2 .small, h3 .small, h4 .small, h5 .small, h6 .small, .h1 .small, .h2 .small, .h3 .small, .h4 .small, .h5 .small, .h6 .small {
    // color: $dark-gray;
    color: #7e899a; // Added from wp-old-styles
    font-weight: $font-weight-light;
    line-height: $line-height-general;
}

h1 small, h2 small, h3 small, h1 .small, h2 .small, h3 .small {
    font-size: 60%;
}

h1 .subtitle {
    display: block;
    margin: 0 0 $margin-large-vertical;
}

.text-muted {
    color: $dark-gray;
}

.text-primary, .text-primary:hover {
    color: #1D62F0 !important;
}

.text-info, .text-info:hover {
    color: $info-color !important;
}

.text-success, .text-success:hover {
    color: $success-color !important;
}

.text-warning, .text-warning:hover {
    color: $warning-color !important;
}

.text-danger, .text-danger:hover {
    color: $danger-color !important;
}

.text-space {
    padding: 70px 0;
}

@font-face {
    font-family: 'Glyphicons Halflings';
    src: url("../fonts/glyphicons-halflings-regular.eot");
    src: url("../fonts/glyphicons-halflings-regular.eot?#iefix") format("embedded-opentype"), url("../fonts/glyphicons-halflings-regular.woff") format("woff"), url("../fonts/glyphicons-halflings-regular.ttf") format("truetype"), url("../fonts/glyphicons-halflings-regular.svg#glyphicons-halflingsregular") format("svg");
}
